import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CookieService } from 'ngx-cookie-service';
import { TodoListService } from '../../../api/controllers/TodoList';

@Component({
  selector: 'app-todo-create-category',
  templateUrl: './todo-create-category.component.html',
  styleUrls: ['./todo-create-category.component.scss']
})
export class TodoCreateCategoryComponent implements OnInit {

  public category = {
    "todoListCategoryId": 0,
    "apartmentId": parseInt(this.cookieService.get('apartmentId')),
    "staffId": 0,
    "categoryName": "",
    "isActive": true,
    "insertedBy": 0,
    "insertedOn": "2020-06-09T07:29:07.302Z",
    "updatedBy": 0,
    "updatedOn": "2020-06-09T07:29:07.302Z"
  }

  constructor(private cookieService: CookieService, public dialogRef: MatDialogRef<TodoCreateCategoryComponent>, private todoService: TodoListService,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      if(this.data && this.data.type == 'edit'){
        for (const key in this.data) {
         this.category[key] = this.data[key]
        }
      }
  }

  ngOnInit(): void {
  }

  createCategory() {
    this.category.insertedBy = parseInt(this.cookieService.get('userId'));
    this.category.insertedOn = new Date().toISOString();
    this.category.updatedOn = new Date().toISOString();
    this.category.updatedBy = 0;
    let createCategoryParam = {
      todoListCategoryModel: this.category
    }
    this.todoService.addTodoListCategory(createCategoryParam).subscribe(
      resp => {
        this.dialogRef.close()
      },
      errro => {

      }
    )
  }

  updateCategory() {
    this.category.insertedBy = 0;
    this.category.insertedOn = new Date().toISOString();
    this.category.updatedOn = new Date().toISOString();
    this.category.updatedBy = parseInt(this.cookieService.get('userId'));
    let updateCategory = {
      todoListCategoryModel: this.category
    }
    this.todoService.updateTodoListCategory(updateCategory).subscribe(
      resp => {
        this.dialogRef.close()
      },
      errro => {

      }
    )
  }

}
