import { Component, OnInit, ViewChild, AfterViewInit, Injector } from '@angular/core';
import { jqxSchedulerComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxscheduler';
import { jqxButtonComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxbuttons';
import { CookieService } from 'ngx-cookie-service';
import { MeetingService } from 'src/app/api/controllers/Meeting';
import * as moment from 'moment';
import { MatDialog } from '@angular/material/dialog';
import { MeetingEditDisplayComponent } from '../../meetings/components/meeting-edit-display/meeting-edit-display.component';
import { TodoCreateDialogComponent } from '../todo-create-dialog/todo-create-dialog.component';
import { TodoCreateCategoryComponent } from '../todo-create-category/todo-create-category.component';
import { TodoListService } from '../../../api/controllers/TodoList';

@Component({
  selector: 'app-todo-create',
  templateUrl: './todo-create.component.html',
  styleUrls: ['./todo-create.component.scss']
})
export class TodoCreateComponent implements OnInit {

  @ViewChild('scheduler', { static: false }) myScheduler: jqxSchedulerComponent;
  schedulerList: any;
  date: any = new jqx.date(new Date());
  dataAdapter: any;
  source: any = {
    dataType: 'array',
    dataFields: [
      { name: 'id', type: 'string' },
      { name: 'description', type: 'string' },
      { name: 'location', type: 'string' },
      { name: 'subject', type: 'string' },
      { name: 'calendar', type: 'string' },
      { name: 'start', type: 'date' },
      { name: 'end', type: 'date' }
    ],
    id: 'id',
  };
  printButton: any = null;
  resources: any = {
    colorScheme: 'scheme05',
    dataField: 'calendar',
    source: new jqx.dataAdapter(this.source)
  };
  appointmentDataFields: any = {
    from: 'start',
    to: 'end',
    id: 'id',
    description: 'description',
    location: 'place',
    subject: 'subject',
    resourceId: 'calendar'
  };
  views: string[] | any[] = [
    'dayView',
    'weekView',
    'monthView',
    'agendaView'
  ];
  public categoryList: Array<any> = [];
  constructor(
    private meetingService: MeetingService,
    private cookieService: CookieService,
    private injector: Injector,
    private todoService: TodoListService,
    public dialog: MatDialog
  ) { }

  getWidth(): any {
    // if (document.body.offsetWidth < 800) {
    //   return '90%';
    // }
    return '100%';
  }

  ngOnInit(): void {
    this.getMeetingList();
  }

  getCategoryList() {
    let getCategoryParam = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.todoService.getAllTodoListCategorysByApartmentId(getCategoryParam).subscribe(
      (resp: any) => {
        this.categoryList = resp;
      },
      error => {

      }
    )
  }

  appointmentClick(event) {
    let data: any = event.args.appointment.originalData;
    data['type'] = 'edit';
    const dialogRef = this.dialog.open(TodoCreateDialogComponent, {
      width: 'auto',
      height: 'auto',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getMeetingList();
      }
    });
  }

  appointmentAdd(event) {
    this.myScheduler.closeDialog();
    let data = { type: 'create' };
    const dialogRef = this.dialog.open(TodoCreateDialogComponent, {
      width: 'auto',
      height: 'auto',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getMeetingList();
      }
    });
  }

  openCategory() {
    let data = { type: 'create' };
    const dialogRef = this.dialog.open(TodoCreateCategoryComponent, {
      width: '500px',
      height: 'auto',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getCategoryList();
      }
    });
  }

  editCategory(id) {
    this.todoService.getTodoListCategoryById(id).subscribe(
      (resp) => {
        let data = resp;
        data['type'] = 'edit'
        const dialogRef = this.dialog.open(TodoCreateCategoryComponent, {
          width: '500px',
          height: 'auto',
          data: data
        });
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.getCategoryList();
          }
        });
      },
      error => {

      }
    )
  }

  onDialogClose(event) {
    console.log('e', event)
  }

  getMeetingList() {
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      staffId:0
    }
    this.todoService.getTodoListByStaffId(params).subscribe(
      (resp:any) =>{
        if (resp.length > 0) {
          this.schedulerList = [];  
          resp.forEach((data, i) => {
            let day =  moment(data.finishOn).day();
            let entity = {
              id: data.todoListId,
              description: data.description,
              location: '',
              subject: data.title,
              calendar: `Room ${day}`,
              start: this.formStartDate(data.beginOn),
              end: this.formEndDate(data.finishOn)
            }
            this.schedulerList.push(entity);
          });
          this.source.localdata = this.schedulerList;
          this.dataAdapter = new jqx.dataAdapter(this.source);
          //this.resources.source = new jqx.dataAdapter(this.source);
        }
      }
    )
  }
  formStartDate(data){
    let year  = moment(data).year();
    let month  = moment(data).month();
    let date  = moment(data).date();
    let startHours  = moment(data,'HH:mm').hour();
    let startMin  = moment(data,'HH:mm').minute();
    return new Date(year,month,date,startHours,startMin)
  }
  formEndDate(data){
    let year  = moment(data).year();
    let month  = moment(data).month();
    let date  = moment(data).date();
    let endHours  = moment(data.toTime,'HH:mm').hour();
    let endMin  = moment(data.toTime,'HH:mm').minute();
    return new Date(year,month,date,endHours,endMin);
  }
  ngAfterViewInit() {
    this.myScheduler.ensureAppointmentVisible('id1');
  }

}
