import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { TodoListService } from '../../../api/controllers/TodoList';
import { CookieService } from 'ngx-cookie-service';
import { TodoCreateDialogComponent } from '../todo-create-dialog/todo-create-dialog.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-todo-list',
  templateUrl: './todo-list.component.html',
  styleUrls: ['./todo-list.component.scss']
})
export class TodoListComponent implements OnInit {
 
  public stepperList:Array<any>=[];
  gridSourceData: any;
  todoList: Array<any> = [];

  constructor(private todoService: TodoListService,private cookieService:CookieService,public dialog : MatDialog ) {

  }

  ngOnInit(): void {
    this.stepperList=[
      {
        name:'Open',
        color:'#fa7b7b',
        isActive:true
      },
      {
        name:'InProgress',
        color:'#ff9800',
        isActive:true
      },
      { 
        name:'Closed',
        color:'#4caf50',
        isActive:false
      },
    ]
    this.getTodoList();
  }


  getTodoList() {
    let getList ={
      apartmentId:parseInt(this.cookieService.get('apartmentId'))
    }
    this.todoService.getAllTodoListsByApartmentId(getList).subscribe(
      (resp: any) => {
        this.todoList = resp.filter(data => {
          return data.isActive
        });
      },
      error => {

      }
    )
  }

  openEdit(id){
    let data:any;
    data['id']=id;
    data['type'] = 'edit';
    const dialogRef = this.dialog.open(TodoCreateDialogComponent, {
      width: 'auto',
      height: 'auto',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getTodoList();
      }
    });
  }
  deleteList(id){
    let deleteParam ={
      todoListId:id,
      deleteBy:parseInt(this.cookieService.get('userId'))
    }
    this.todoService.deleteTodoList(deleteParam).subscribe(
      (resp: any) => {
        this.getTodoList();
      },
      error => {

      }
    )
  }
  changeStatus(obj,category){
    let updateTodo = obj;
    if(category == 'inprogress'){
      updateTodo.todoStatusId = 217;
    }else if(category == 'completed'){
      updateTodo.todoStatusId = 218;
    }
    updateTodo.insertedBy = parseInt(this.cookieService.get('userId'));
    updateTodo.insertedOn = new Date().toISOString();
    updateTodo.updatedOn = new Date().toISOString();
    updateTodo.updatedBy = 0;updateTodo
    let updateTodoParam ={
      todoListModel : updateTodo
    }
    this.todoService.updateTodoList(updateTodoParam).subscribe(
      resp =>{
        this.getTodoList();
      }
    )
  }

  getPriority(id){
    if(id == 205){
      return '4px solid #f44336'
    }else if(id == 206){
      return '4px solid #FF9800'
    }else{
      return '4px solid #ffc107'
    }
  }
}
