import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TodoListComponent } from './todo-list/todo-list.component';
import { TodoCreateComponent } from './todo-create/todo-create.component';
import { TodoReminderComponent } from './todo-reminder/todo-reminder.component';
import { TodoHistoryComponent } from './todo-history/todo-history.component';




const routes: Routes = [
	{ path: '', redirectTo: 'todo-creation', pathMatch: 'full'},
	{ path: 'todo-creation', component: TodoCreateComponent},
	{ path: 'todo-list', component: TodoListComponent},
	{ path: 'todo-reminder', component: TodoReminderComponent},
	{ path: 'todo-history', component: TodoHistoryComponent},
	{ path: '**', redirectTo: 'todo-creation', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TodoRouting { }
