import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LookupService } from '../../../api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';
import { TodoListService } from '../../../api/controllers/TodoList';
import * as moment from 'moment';

@Component({
  selector: 'app-todo-create-dialog',
  templateUrl: './todo-create-dialog.component.html',
  styleUrls: ['./todo-create-dialog.component.scss']
})
export class TodoCreateDialogComponent implements OnInit {


  public statusList: Array<any> = [];
  public priorityList: Array<any> = [];
  public categoryList: Array<any> = [];
  public startTime='';
  public endTime='';
  public todoParam ={
    "todoListId": 0,
    "apartmentId": parseInt(this.cookieService.get('apartmentId')),
    "emailId": "",
    "description": "",
    "beginOn": "",
    "finishOn": "",
    "todoStatusId": 0,
    "isActive": true,
    "insertedBy": 0,
    "insertedOn": "",
    "updatedBy": 0,
    "updatedOn": "",
    "staffId": 0,
    "title": "",
    "priorityId": 0,
    "todoListCategoryId": 0
  }

  constructor(public dialogRef: MatDialogRef<TodoCreateDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private lookUpService: LookupService, private cookieService: CookieService,private todoService:TodoListService) {
  }

  ngOnInit(): void {
    this.getMasterData();
    if(this.data && this.data.type == 'edit'){
      if(this.data){
        this.getTodoData(this.data.id);
      }
    }
  }

  getTodoData(id){
    let getTodoParam ={
      apartmentId:parseInt(this.cookieService.get('apartmentId')),
      todoListId : id
    }
    this.todoService.getTodoListById(getTodoParam).subscribe(
      (resp: any) => {
        for (const key in resp) {
           this.todoParam[key] = resp[key];
        }
      },
      error => {

      }
    )
  }

  getMasterData() {
      let getCategoryParam = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }
      this.todoService.getAllTodoListCategorysByApartmentId(getCategoryParam).subscribe(
        (resp: any) => {
          this.categoryList = resp.filter(key => {
            return key.isActive;
          })
        },
        error => {
  
        }
      )
    let statusParam = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 192
    }
    let priorityParam = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 191
    }
    this.lookUpService.getLookupValueByLookupTypeId(statusParam).subscribe(
      (resp: any) => {
        this.statusList = resp.filter(key => {
          return key.isActive;
        })
      }
    )
    this.lookUpService.getLookupValueByLookupTypeId(priorityParam).subscribe(
      (resp: any) => {
        this.priorityList = resp.filter(key => {
          return key.isActive;
        })
      }
    )
  }


  createTODO(){
    let createTodo = this.todoParam;
    createTodo.insertedBy = parseInt(this.cookieService.get('userId'));
    createTodo.insertedOn = new Date().toISOString();
    createTodo.updatedOn = new Date().toISOString();
    createTodo.updatedBy = 0;
    createTodo.beginOn = this.setTime(this.todoParam.beginOn,moment(this.startTime,'HH:mm'));
    createTodo.finishOn = this.setTime(this.todoParam.finishOn,moment(this.endTime,'HH:mm'));
    let addTodoParam ={
      todoListModel : createTodo
    }
    this.todoService.addTodoList(addTodoParam).subscribe(
      resp =>{
        this.dialogRef.close();
      }
    )
  }


  editTODO(){
    let updateTodo = this.todoParam;
    updateTodo.insertedBy = parseInt(this.cookieService.get('userId'));
    updateTodo.insertedOn = new Date().toISOString();
    updateTodo.updatedOn = new Date().toISOString();
    updateTodo.updatedBy = 0;
    updateTodo.beginOn = this.setTime(this.todoParam.beginOn,moment(this.startTime,'HH:mm'));
    updateTodo.finishOn = this.setTime(this.todoParam.finishOn,moment(this.endTime,'HH:mm'));
    let updateTodoParam ={
      todoListModel : updateTodo
    }
    this.todoService.updateTodoList(updateTodoParam).subscribe(
      resp =>{
        this.dialogRef.close();
      }
    )
  }

  setTime(date,time){
    return date.set({
      hour:   time.get('hour'),
      minute: time.get('minute'),
      second: time.get('second')
  });
  }

}
